const getIconFlowType = (flowType: string | undefined) => {
  if (flowType === undefined) return <Feed fontSize="small" />
  switch (flowType.toLocaleLowerCase()) {
    case "daily briefing":
      return <Newspaper fontSize="small" />

    case "company analysis":
      return <Business fontSize="small" />

    case "earnings analysis":
      return <BarChart fontSize="small" />

    case "sector analysis":
      return <TrendingUp fontSize="small" />

    case "portfolio analysis":
      return <PieChart fontSize="small" />
    case "market wrap":
      return <CalendarMonth fontSize="small" />

    default:
      return <Feed fontSize="small" />
  }
}

